.card {
    background: var(--light-background-color);
    height: 200px;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
}

.card-image {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    transition: .5s;
    transform: translateY(0);
    border-radius: 8px;
    overflow: hidden;
}

.card:hover .card-image {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    transform: translateY(-100%);
    z-index: 1000;
    padding: 0 20px;
}

.card-image img {
    width: 100%;
    height: 100%;
    border: none;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.card-image:hover img {
    width: 100%;
    height: 100%;
    border: none;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    box-shadow: 0 6px 30px var(--card-shadow-color);
}

.card-description {
    height: 100%;
    width: 100%;
    padding: 30px 20px 20px 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(100%);
    transition: .5s;
}

.card:hover .card-description {
    height: 100%;
    width: 100%;
    padding: 30px 20px 20px 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(0);
}

.card-title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 10px;
    letter-spacing: 0.025em;
    line-height: 1.6em;
}

.card-title>a {
    text-decoration: none;
    color: var(--heading-color);
}

.card-tools {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
}

.card-tools li {
    list-style: none;
    font-size: 11px;
    background: var(--background-color);
    padding: 6px 8px;
    border-radius: 30px;
    text-align: center;
    font-weight: 400;
}